.loader-overlay {
    position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  background: linear-gradient(to bottom, #000000, #0a0f0a, #0f2610);
  z-index: 9999;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transform: translateY(0%);
  transition: transform 0.7s ease-in-out;
  overflow: hidden;
  }
  
  .spinner {
    border: 8px solid #ccc;
    border-top: 8px solid #39ff14;
    border-radius: 50%;
    width: 60px;
    height: 60px;
    animation: spin 0.8s linear infinite;
    margin-bottom: 1rem;
  }

  .loader-overlay.fade-out {
    transform: translateY(-100%);
    background-color: black;
    box-shadow: 0 0 20px #39ff14, 0 0 40px #39ff14 inset;
  }
  
  .loader-text {
    font-size: 1.3rem;
  color: #39ff14;
  text-shadow: 0 0 5px #39ff14;
  font-family: 'Courier New', Courier, monospace;
  z-index: 1;
  transition: opacity 0.3s ease;
  }
  
  .loader-overlay.fade-out .loader-text,
  .loader-overlay.fade-out .spinner {
    opacity: 0;
}

  @keyframes spin {
    to {
      transform: rotate(360deg);
      box-shadow: 0 0 20px #39ff14, 0 0 40px #39ff14 inset;
    }
  }
  