* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'PT Sans', sans-serif;
}

html, body {
  height: 100%;
  margin: 0;
  padding: 0;
  background: none;
  background-color: transparent;
}

.home,
.Projects,
.Contact,
.Resume {
  display: flex;
  /* height: 90vh; */
  align-items: center;
  justify-content: center;
  font-size: 3rem;
}

/* Updated layout for Projects section */
.projects-wrapper {
  min-height: 100vh;
  width: 100%;
  background-image: url('./assets/images/img-14.jpg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: scroll;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  position: relative;
}

/* Projects content inside the wrapper */
.Projects {
  flex: 1;
  overflow-y: auto;
  padding: 80px 20px 60px; /* Top padding avoids navbar clipping */
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center; /* Center cards horizontally */
  z-index: 1;
}

/* Contact section */
.Contact {
  background-image: url('./assets/images/img-1.jpg');
  background-position: center;
  background-size: fill;
  height: 100vh;
  padding: 100px 20px;
  align-items: flex-start;
  background-repeat: no-repeat;
  color: #fff;
  padding-top: 150px;
  font-size: 50px;
}

/* Resume page */
.Resume {
  background-image: url('./assets/images/img-10.jpg');
  min-height: 100vh;
  overflow-y: auto;
  width: 100%;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
}

.Resume iframe {
  background-color: transparent;
}

.projects-heading {
  max-width: 1200px;
  margin: 0 auto;
  text-align: top;
}

.resume-video-bg {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
}

/* Contact Page Styling */
.contact-container {
  min-height: 100vh;
  padding-top: 150px;
  background: url('./assets/images/img-12.webp') center center/cover no-repeat;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  color: #fff;
  text-align: center;
}

.contact-heading {
  font-size: 3rem;
  color: white;
  text-shadow: 0 0 10px black, 0 0 20px black;
  margin-bottom: 1rem;
  margin-top: 100px;
  font-family: Georgia, 'Times New Roman', Times, serif;
}

.contact-methods {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.5rem;
}

.contact-link {
  font-size: 1.5rem;
  text-decoration: none;
  font-family: Georgia, 'Times New Roman', Times, serif;
  color: white;
  transition: transform 0.2s ease;
  justify-content: center;
}

.contact-link:hover {
  transform: scale(1.1);
}

.contact-description {
  font-size: 1.5rem;
  color: white;
  text-shadow: 0 0 10px black, 0 0 20px black;
  margin-bottom: 2rem;
}

.glow {
  text-shadow: 0 0 8px black;
}

.project-grid {
  display: flex;
  flex-direction: column;
  align-items: center;       /* Centers children horizontally */
  justify-content: flex-start;
  width: 100%;
  max-width: 900px;          /* Prevents cards from stretching too wide */
  margin: 0 auto;            /* Ensures centering in wider viewports */
  padding-bottom: 80px;
  gap: 2rem; 
}

/* Enable background-attachment on larger screens only */
@media (min-width: 768px) {
  .projects-wrapper {
    background-attachment: fixed;
  }
  .project-grid {
    width: 100%;
    padding: 0 1rem;
    align-items: center;
  }

  .Cards__item {
    margin: 0 auto;
  }

  .Projects {
    padding: 140px 10px 60px;
  }
}

.fade-in {
  opacity: 0;
  animation: fadeInPage 1s ease .9s forwards;
}

@keyframes fadeInPage {
  to {
    opacity: 1;
  }
}
