video{
    object-fit: cover;
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: -1;
}

.hero-container {
    /* background: url('./assets/images/img-home.jpg') center center/cover no-repeat; */
    height: 100vh;
    display: flex;
    flex-direction: column;
    position: relative;
    justify-content: center;
    overflow: hidden;
    opacity: 0;
    animation: fadeInText .5s ease .05s forwards;
    width: 100%;  
    align-items: center;
    text-align: center;
    color: #fff;
    z-index: 1;
    padding: 0 20px;
}


@keyframes fadeInText {
  to {
    opacity: 1;
  }
}

@keyframes fadeInNeon {
  to {
    opacity: 1;
  }
}

@keyframes fadeInGlow {
  to {
    opacity: 1;
  }
}

.hero-container h1 {
    font-size: 4rem;
    margin: 0 auto 1rem auto;
    margin-top: -20rem;
    text-align: center;
    text-shadow: 0 2px 10px rgba(0, 0, 0, 0.6);
    font-family:Georgia, 'Times New Roman', Times, serif
  }

  .hero-container p {
    font-size: 1.2rem;
    margin-bottom: 2rem;
    text-shadow: 0 2px 10px rgba(0, 0, 0, 0.5);
    max-width: 600px;
    margin-top: 1rem;

    font-family:Georgia, 'Times New Roman', Times, serif

  }

  .hero-container h1,
  .hero-container p {
  color: white;
  text-shadow:
    0 0 5px #37ff142c,
    0 0 10px #37ff142c,
    0 0 20px #37ff142c;
  opacity: 0;
  animation: fadeInGlow 1.2s ease .1s forwards;
  }
  .hero-btns .btn {
    margin: 0.5rem;
  }

  .faPlay {
    font-size: 2rem;
    margin-left: 0.5rem;
    color: #fff;
  }

  @media screen and (max-width: 960px) {
    .hero-container h1 {
      font-size: 2.5rem;
      margin-top: -300px;
    }

    .hero-container p {
      font-size: 1.5rem;
    }
    
  }

  @media screen and (max-width: 768px) {
    .hero-container h1 {
      font-size: 2rem;
      /* margin: 2rem auto 1rem auto; */
      align-items: flex-start;
      padding-top: -5rem;
      justify-content: flex-start;
    }

    .hero-content {
      padding: 1rem;
    }

    .hero-container p {
      font-size: 1rem;
    }

    .btn-mobile {
        display: block;
        text-decoration: none;
      }

      .btn{
        width: 100%;
      }

      .hero-video {
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        display: none;
        object-fit: cover;
        z-index: -1;
        pointer-events: none;
      }
    
  }
  .hero-video {
    display: none;
  }