.social-container {
    display: flex;
    justify-content: center;
    gap: 2rem;
    padding: 1rem 2rem;
    border: 3px solid #39ff14;
    border-radius: 100px;
    background-color: rgba(10,8,20,0.9); /* or match your site bg */
    width: fit-content;
    margin: auto;
    margin-bottom: 2rem;
  }
  
  .social-container a {
    text-decoration: none;
    color: black;
    background-color: #39ff14;
    padding: 0.75rem;
    border-radius: 50%;
    font-size: 1.5rem;
    transition: transform 0.2s ease, box-shadow 0.3s ease;
    background: 0.2s ease;
    box-shadow: 0 0 10px #39ff14;
  }
  
  .social-container a:hover {
    transform: scale(1.1);
    background-color: #00ff99;
    color: black;
    box-shadow: 0 0 15px #00ff99, 0 0 30px #00ff99;
  }

  @media screen and (max-width: 768px) {
  .social-container {
    gap: 1rem;
    padding: 0.5rem 1rem;
    flex-wrap: wrap;
  }

  .social-container a {
    padding: 0.5rem;
    font-size: 1.1rem;
    width: 2.5rem;
    height: 2.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}