.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(10, 10, 10, 0.95);
  width: 100vw;
  height: 100vh;
  overflow-y: auto; /* <-- Allow scrolling */
  z-index: 1000;
  display: flex;
  justify-content: center;
  align-items: flex-start; /* <-- Top-align the modal */
  padding-top: 10vh; /* <-- Add spacing from top */
  padding-bottom: 5vh;
  }
  
  .modal-box {
    background-color: #111;
    color: #fff;
    padding: 2rem;
    border-radius: 16px;
    max-width: 500px;
    width: 90%;
    box-shadow: 0 0 25px #39ff14;
    position: relative;
    overflow-y: auto;
    max-height: none; /* <-- Limit height */
    margin-top: 1rem;
  }
  
  .modal-close-button {
    position: absolute;
    top: 10px;
    right: 14px;
    font-size: 2rem;
    background: none;
    border: none;
    color: #39ff14;
    cursor: pointer;
  }
  
  .modal-title {
    font-size: 1.8rem;
    color: #39ff14;
    text-shadow: 0 0 8px #39ff14;
    margin-bottom: 1rem;
  }
  
  .modal-image {
    width: 100%;
    border-radius: 12px;
    margin-bottom: 1.5rem;
  }
  
  .modal-description {
    font-size: 1rem;
    color: #ccc;
    margin-bottom: 1rem;
  }
  
  .modal-course {
    font-size: 0.95rem;
    margin-bottom: 1rem;
    color: #aaa;
  }
  
  .modal-tags {
    display: flex;
    flex-wrap: wrap;
    gap: 0.5rem;
    margin-bottom: 1.5rem;
  }
  
  .modal-tag {
    background: #1a1a1a;
    border: 1px solid #39ff14;
    padding: 0.3rem 0.6rem;
    border-radius: 20px;
    font-size: 0.85rem;
    color: #39ff14;
    text-shadow: 0 0 4px #39ff14;
  }
  
  .modal-subheading {
    font-size: 1.2rem;
    margin-top: 1.5rem;
    margin-bottom: 0.5rem;
    font-weight: bold;
    color: #39ff14;
  }
  
  .modal-list {
    list-style-type: disc;
    padding-left: 1.5rem;
    color: #ddd;
    font-size: 0.95rem;
  }

.modal-description,
.modal-course,
.modal-list {
  font-size: 0.9rem;
}
  